<template>
	<section
		class="navbar custom-navbar navbar-fixed-top"
		style="height: 85px"
		id="navbar-top">
		<div class="container">
			<div class="navbar-header">
				<!-- lOGO -->
				<a class="navbar-brand">CHenK</a>
			</div>
			<!-- MENU LINKS -->
			<div class="collapse navbar-collapse">
				<ul class="nav navbar-nav navbar-nav-first">
					<li>
						<a
							href="#about"
							@click="handleSelect(1)"
							class="smoothScroll"
							>CSS笔记</a
						>
					</li>
					<li>
						<a
							href="#about"
							@click="handleSelect(2)"
							class="smoothScroll"
							>Vue笔记</a
						>
					</li>
					<li>
						<a
							href="#about"
							@click="handleSelect(3)"
							class="smoothScroll"
							>JavaScript笔记</a
						>
					</li>
					<li>
						<a
							href="#about"
							@click="handleSelect(4)"
							class="smoothScroll"
							>Typescript笔记</a
						>
					</li>
				</ul>
				<ul class="nav navbar-nav navbar-right">
					<li class="section-btn">
						<a
							href="#about"
							class="smoothScroll"
							@click="handleSelect(5)"
							>个人笔记</a
						>
					</li>
				</ul>
			</div>
		</div>
	</section>

	<!-- HOME -->
	<section
		id="home"
		data-stellar-background-ratio="0.5">
		<div class="overlay"></div>
		<div class="container">
			<div class="row">
				<div class="col-md-6 col-sm-12">
					<div
						class="home-info"
						style="
							display: flex;
							flex-direction: column;
							align-items: center;
						">
						<h1 style="text-align: center">进入我的笔记</h1>
						<a
							href="#about"
							class="btn section-btn smoothScroll"
							@click="handleSelect(5)"
							>开始</a
						>
					</div>
				</div>
				<div class="col-md-6 col-sm-12">
					<div class="home-video">
						<div class="embed-responsive embed-responsive-16by9">
							<div class="home-video__img">
								<img
									:src="require('@/assets/img/footer.png')" />
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>

	<div
		style="height: calc(100vh - 8vh - 20px)"
		id="about">
		<keep-alive>
			<component :is="component" />
		</keep-alive>
	</div>
</template>

<script setup>
	// import $ from 'jquery';
	import '../../../assets/js/jquery.js';
	import '../../../assets/js/custom.js';

	import ICSS from './components/ICSS.vue';
	import IJS from './components/IJS.vue';
	import IVUE from './components/IVUE.vue';
	import ITS from './components/ITS.vue';
	import MAIN from './components/MAIN.vue';

	import { ref, computed, onMounted } from 'vue';
	const activeIndex = ref('5');
	const components = { 1: ICSS, 2: IVUE, 3: IJS, 4: ITS, 5: MAIN };
	const component = computed(() => components[activeIndex.value]);

	const handleSelect = (key) => {
		activeIndex.value = key;
	};

	//  jQuery 中的 animate 函数
	$(document).ready(function () {
		// 为 smoothScroll 的元素添加一个点击事件
		// 在点击事件中，页面会平滑滚动到被点击元素 href 属性值所指向的元素处
		$('.smoothScroll').on('click', function (event) {
			if (this.hash !== '') {
				event.preventDefault();
				var hash = this.hash;

				// 获取导航栏的高度
				var navbarHeight = $('#navbar-top').outerHeight();

				// 获取目标元素的顶部位置并减去导航栏的高度
				var targetOffset = $(hash).offset().top - navbarHeight;

				$('html, body').animate(
					{
						scrollTop: targetOffset,
					},
					500,
					function () {
						// 500 指的是滚动动画的时长（单位为毫秒），可以根据需要调整。
						window.location.hash = hash;
					}
				);
			}
		});
	});

	//第一次载入页面防止页面样式被其他组件影响进行刷新的机制
	onMounted(() => {
		if (!localStorage.getItem('refreshed')) {
			// 在本地存储中设置一个标记，指示页面已刷新
			localStorage.setItem('refreshed', 'true');

			// 刷新页面，这条语句会导致页面重新加载
			window.location.reload();
		} else {
			// 页面已经被刷新，现在清除标记以便下次能够刷新
			localStorage.removeItem('refreshed');
		}
	});
</script>

<style scoped>
	@import '../../../assets/css/bootstrap.min.css';
	@import '../../../assets/css/templatemo-style.css';
	@import '../../../assets/css/bootstrap.min.css';
	body {
		display: block !important;
	}
</style>
